import {
  JSONSchema7,
} from 'json-schema';

type JSONSchemaType = 'string' | 'number' | 'object' | 'array' | 'integer';

abstract class IJsonschema {
  $schema?: string;
  $id?: string;
  type!: JSONSchemaType;
  title?: string;
  description?: string;
  default?: {
    [k: string]: string
  };
  required?: string[];
  items?: any[];
  properties?: {
    [k: string]: IJsonschema
  };

  oneOf?: IJsonschema[];
  const?: JSONSchemaType;

  // number
  maximum?: number;
  minimum?: number;
  exclusiveMaximum?: number;
  exclusiveMinimum?: number;

  // array
  minItems?: number;

  // string
  minLength?: number;
  maxLength?: number;

  // 
  /**  */
  sort?: string[];
  /**  */
  custom?: boolean;
  /**  */
  customType?: string;
  /** JSONschema2Type */
  validator?: string[];
  /** classvueclass */
  class?: string[] | string;
  /** stylevuestyle */
  style?: string | object;
  /** form-itemvueclass */
  wrapperClass?: string[] | string;
  /** form-itemvuestyle */
  wrapperStyle?: string | object;
  /**  */
  disabled?: boolean;
  /** form-item */
  notNeedFormItem?: boolean;
  /** valuevaluekey */
  valueKey?: string;
  /** vlabellabelkey */
  labelKey?: string;
  /**  */
  inputType?: 'text' | 'password' | 'email' | 'textarea' | 'color' | 'url';
  /** vuev-show */
  show?: boolean;
  /** */
  needCalculateItems?: (keyof Pick<IJsonschema, 'show' | 'disabled'>)[];
  /**  */
  [k: string]: unknown;
}

export class Jsonschema extends IJsonschema implements JSONSchema7 {
  constructor(jsonsch: Partial<IJsonschema> & NonNullable<Pick<IJsonschema, 'type'>>) {
    super();
    Object.assign(this, jsonsch);
    return this;
  }
  // setSchemaVersion(target: IJsonschema) {
  //   target.$schema = 'http://json-schema.org/draft-07/schema';
  //   return this;
  // }
}

export default Jsonschema;
